import { MAKE_NAVBAR_FALSE,MAKE_NAVBAR_TRUE }from '../actions/types'
const INITIAL_STATE = {
    navbar: true,
};

export default (state = INITIAL_STATE,action)=>{
    switch (action.type) {
        case MAKE_NAVBAR_FALSE:
            return { 
                ...INITIAL_STATE,
                navbar : false
            }
        case MAKE_NAVBAR_TRUE:
            return{
                ...INITIAL_STATE,
                navbar : true
            }
        default:
            return state;
    }


}