import React from 'react';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter,Input } from 'reactstrap'
import Cookies from "universal-cookie";
import {connect} from 'react-redux';
import { Redirect, Link } from 'react-router-dom'
import { tryout_masuk } from '../../../actions/index';


const cookies = new Cookies();
class ModalConfirm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      modal: false
    };

    this.toggle = this.toggle.bind(this);
  }

  toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }
  
  onClickBtn = () => {
    this.props.tryout_masuk()
    cookies.set('navbar',true,{path: '/'})
  }
  render() {
    return (
      <div>
        <Button color="danger" onClick={this.toggle}>{this.props.buttonLabel}</Button>
        <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
          <ModalHeader toggle={this.toggle}>Pilih Mata Pelajaran</ModalHeader>
          <ModalBody>
          <Input type="select" name="select" id="exampleSelect">
            <option>Matematika</option>
            <option>Fisika</option>
            <option>Kimia</option>
            <option>Biologi</option>
          </Input>
          <br></br>
          <div className='container'>
                1. Ujian dimulai sejak tombol mulai dipencet <br></br>
                2. Jangan tekan tombol selesai sebelum yakin dengan jawabanmu
            </div>
          </ModalBody>
          <ModalFooter>
            <Link to='/tryout/1'> <Button color="primary" onClick={this.onClickBtn}>Mulai</Button> </Link>{' '}
            <Button color="secondary" onClick={this.toggle}>Kembali</Button>
          </ModalFooter>
        </Modal>
      </div>
    );
  }
}
const mapStateToProps =(state)=>{
    return {
        username: state.auth.username,
        id:state.auth.id,
        error: state.auth.error,
        loading: state.auth.loading
    
    };
}
export default connect(mapStateToProps,{tryout_masuk})(ModalConfirm);