import React,{Component} from 'react';
import Navbar from './navbarTryOut'
import {connect} from 'react-redux';
import { tryout_masuk } from '../../../actions'
import '../../../support/css/tryout/radioButton.css'
import Axios from 'axios';

class tryotPage extends Component{
    state = {
        soaldetail : [],
        pilihan : []
    }
    componentDidMount(){ 
        this.getDetailSoal()
    }

    getDetailSoal = () => {
        var nomor = this.props.match.params.no
        Axios.get('http://localhost:2001/soaldetail/' + nomor)
        .then((res) => {
            this.setState({soaldetail : res.data,
                            pilihan : res.data[0].pilihan})
            console.log(this.state.pilihan)
        }).catch((err) => {
            console.log(err)
        })
        
    }
    
    renderSoal = () => {

    }
    renderPilihan = () => {
        var abcde = ['A','B','C','D','E']
        var renderJsx = this.state.pilihan.map((val,index) => {
            return(
                <label className="containerRadio">{abcde[index]}. {val}
                <input type="radio" name='1' defaultChecked="checked" />
                <span className="checkmark" />
                </label>
            )
        })
        return renderJsx
    }
     
    render(){
        return(
            <div>
                
                <div style={{overflow:'scroll', overflowX:'hidden' , height:'600px'}}>
                <Navbar/>
                    <div className='container'>
                    {/* Soal TRYOUT */}
                    <div className="card" style={{marginTop:'25px'}}>
                        <div className="card-header">
                        Bahasa Inggris
                        </div>
                        <div className="card-body">
                        <blockquote className="blockquote mb-0">
                            <p>
                                {/* {this.state.soaldetail[0].pertanyaan} */}
                            </p>
                            <footer>
                                <div>
                                    {this.renderPilihan()}
                                    
                                </div>
                            </footer>
                        </blockquote>
                        </div>
                    </div>
                    </div>
                </div>
                {/* END OF SOAL TRYOUT */}
            </div>
                
        )
  
    }
}
const mapStateToProps =(state)=>{
    return {
        navbar : state.tryout.navbar,
    };
  }

export default connect(mapStateToProps,{tryout_masuk})(tryotPage)