import React,{Component} from 'react';
import {connect} from 'react-redux';
import '../../support/css/homeuser.css';
import Dashboard from './homeUser/dashboard';
import Diskusi from './homeUser/diskusi'
import Teman from './homeUser/teman'
import queryString from 'query-string';
import Berita from './homeUser/berita';
import EnterTryout from './tryout/enterRoom'
import Axios from 'axios'
import Cookies from "universal-cookie";
import {Redirect} from 'react-router-dom'
import ProfileUser from './homeUser/profileUser';

const cookies = new Cookies();
class HomeUser extends Component{
state = {
    nilai : [],
    friendsList:[]
}


componentDidMount(){
const cookienya = cookies.get("dataUser")
const idnya = cookies.get("idUser")
   this.getDataNilai(cookienya) 
   this.getDataTeman(cookienya)

}
getDataNilai=(username)=>{
    Axios.get('http://localhost:2001/nilai/'+username)
    .then((res) => {
        console.log(res.data)
        this.setState({nilai : res.data})
    }).catch((err) => {
        console.log(err)
    })
}
getDataTeman=(username)=>{
    Axios.get('http://localhost:2001/friendSum/'+username)
  .then((res)=>{
      console.log(res.data)
      this.setState({friendsList:res.data})
     
  }).catch((err)=>{
      console.log(err)
  })
  
}

buttonOnDashboardNav=()=>{
    var params = queryString.parse(this.props.location.search)
    var page = params.page;
    var username = params.username
    if(page == 'teman'){
        return <Teman teman={this.state.friendsList}/>
    } else if(page == 'materi'){
        return (<h1>Coming Soon</h1>)
    }else if(page == 'tryout'){
        return <EnterTryout/>
    }else if(page == 'diskusi'){
        return <Diskusi/>
    }else if(page == 'leaderboard'){
        return (<h1>belum jadi</h1>)
    }else if(page == 'berita'){
        return <Berita/>
    }else if(page == 'profile'){
        return <ProfileUser nama={username}/>
    }else{
        return <Dashboard keyId={this.state.nilai}/>
    }
}

    render(){
        const cookienya = cookies.get("dataUser")
        if(cookienya == ''){
            return <Redirect to='/masuk' />
        }else if(this.props.status == 'unverified'){
            console.log(this.props.status)
            return <Redirect to='/verify' />
        }else{
            return(
                <div className="bg-light">
                <div className="nav-scroller bg-white shadow-sm">
                  <nav className="nav nav-underline">
                    <a className="nav-link active" href="/siapptn?page=dashboard">Dashboard</a>
                    {/* <a className="nav-link" href="/siapptn?page=materi">Materi</a> */}
                    <a className="nav-link" href="/siapptn?page=tryout">Tryout</a>
                    <a className="nav-link" href="/siapptn?page=diskusi">Diskusi</a>
                    <a className="nav-link" href="/siapptn?page=teman">
                      Teman
                      <span className="badge badge-pill bg-light align-text-bottom">27</span>
                    </a>
                    <a className="nav-link" href="/siapptn?page=leaderboard">Leader Board</a>
                    <a className="nav-link" href="/siapptn?page=berita">Berita</a>
                  </nav>
                </div>
                <div className="container d-flex align-items-center p-3 my-3 text-white-50 background-dilizents rounded shadow-sm">
                    <img className="mr-3" src={`/image/avatar/${this.props.image}`} alt width={48} height={48} />
                    <div className="w-100">
                    <h2 className="float-right text-white">Siapptn</h2>
                      <h6 className="mb-0 text-white lh-100 w-25">{this.props.username}</h6>
                    
                      <small>Premium <i class='fas fa-check-circle'></i></small>
                      
                    
                    </div>
                   
                  </div>
                <main role="main" className="container">
                  
                 {this.buttonOnDashboardNav()}
                </main>
              </div>
            )
        }
    }
}
const mapStateToProps =(state)=>{
    return {
        username: state.auth.username,
        id : state.auth.id,
        image: state.auth.image,
        status:state.auth.status
    };
}
export default connect(mapStateToProps,null)(HomeUser);