import React from 'react';
import {
    Badge,
    Navbar,
    Nav,
    NavItem,
    Button,
 } from 'reactstrap';
 import SideNav from 'react-simple-sidenav';
import  Cookies  from 'universal-cookie'
import Countdown from 'react-countdown-now';
import { MDBAlert } from 'mdbreact'

const Completionist = () => <span>You are good to go!</span>

const renderer = ({ hours, minutes, seconds, completed }) => {
  if (completed) {
    // Render a completed state
    return <Completionist />;
  } else {
    // Render a countdown
    return <p1> {hours}:{minutes}:{seconds} </p1>
           
  }
};
const cookie = new Cookies()
class NavbarTryout extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false,
      showNav:false,
      redirect : false,
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  onFinishTryout = ()=>{
    cookie.remove('navbar');
  }



  
  render() {
    return (
      <div>
        <SideNav
                    showNav        =  {this.state.showNav}
                    openFromRight={true}
                    onHideNav      =  {() => this.setState({showNav: false})}
                    title          =  "LEMBAR JAWAB"
                    items          =  {[
                    [
                    <Button color='primary' style={{margin:'10px'}}>01</Button>, 
                    <Button color='primary' style={{margin:'10px'}}>02</Button>,
                    <Button color='primary' style={{margin:'10px'}}>03</Button>,
                    <Button color='primary' style={{margin:'10px'}}>04</Button>,
                    <Button color='primary' style={{margin:'10px'}}>05</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>,
                    <Button color='primary' style={{margin:'10px'}}>06</Button>
                    ]
                        ]}
                    titleStyle     =  {{backgroundColor: '#2196F3'}}
                    itemStyle      =  {{backgroundColor: '#fff', listStyleType: 'none' , marginRight:'7px'}}
                    itemHoverStyle =  {{backgroundColor: '#fff'}}
                    />
                  
                    <Navbar color="white" light expand="md" style={{boxShadow:'0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)' , height:'60px'}}>
                      <div className='col-sm-4'> <h4 style={{marginRight:'50px'}}> SOAL NO <Badge color="primary">1</Badge></h4></div>
                      <h4 style={{marginRight:'10px'}}>TIMER = <Badge color="danger"><Countdown  date={Date.now() + 1200000}
                        renderer={renderer}/></Badge></h4>
                      <Nav className="ml-auto" navbar>
                        <NavItem>
                          <Button onClick={()=>this.setState({showNav : true})} color="primary">LEMBAR JAWAB</Button>                
                        </NavItem>
                        <NavItem>
                          <a href='/homes' ><Button onClick={this.onFinishTryout} color="danger">KELUAR</Button></a>               
                        </NavItem>
                    </Nav>
                  </Navbar>               
      </div>
    );
  }
}
export default NavbarTryout;