import React,{Component} from 'react'
import Axios from 'axios';
import {connect} from 'react-redux';
import Cookies from "universal-cookie";
const cookies = new Cookies();
class Followers extends Component{
  state = {
    dataFollowers:[]
}


componentDidMount(){
const idnya = cookies.get("idUser")
   this.getDataFollowers(idnya)

}
  getDataFollowers=(id)=>{
    Axios.get('http://localhost:2001/getFollowers/'+id)
  .then((res)=>{
      this.setState({dataFollowers:res.data})
     
  }).catch((err)=>{
      console.log(err)
  })
  
}
  putDataFollowers=()=>{
    var putdata = this.state.dataFollowers.map((item)=>{
      return(
        <div className="media text-muted pt-3">
        <img src={`./image/avatar/${item.image}`} className="bd-placeholder-img mr-2 rounded" width={32} height={32} />
        <div className="media-body pb-3 mb-0 small lh-125 border-bottom border-gray">
          <div className="d-flex justify-content-between align-items-center w-100">
            <strong className="text-gray-dark">{item.nama}</strong>
            {/* <a href="#">Follow</a> */}
          </div>
          <span className="d-block">@{item.username}</span>
        </div>
      </div>
      )
    })
    return putdata
  }
    render(){
        return(
            <div>
                {this.putDataFollowers()}
                <small className="d-block text-right mt-3">
                  <a href="#">Semua Aktivitas</a>
                </small>
                </div>
        )
    }
}
const mapStateToProps =(state)=>{
  return {
      username: state.auth.username,
      id:state.auth.id
  
  };
}
export default connect(mapStateToProps)(Followers);