import React,{Component} from 'react'
import Axios from 'axios';
import {connect} from 'react-redux';
import '../../../support/css/progress-bar.css'
const Filler =(props)=>{
  return <div className='filler'style={{width:`${props.percentage}%`}}>{props.percentage}%</div>
}
const ProgressBar =(props)=>{
  return(
    <div className="progress-bar">
      <Filler percentage={props.percentage}/>
    </div>
  )
}

class ProfileUser extends Component{
state={
    nilai:[],
    teman:[]
}
componentDidMount(){
    this.getDataNilai()
    this.getDataTeman()
}
getDataNilai=()=>{
    Axios.get('http://localhost:2001/nilai/'+this.props.nama)
    .then((res) => {
        console.log(res.data)
        this.setState({nilai : res.data})
    }).catch((err) => {
        console.log(err)
    })
}
getDataTeman=()=>{
    Axios.get('http://localhost:2001/user/'+this.props.nama)
    .then((res) => {
        console.log(res.data)
        this.setState({teman : res.data})
    }).catch((err) => {
        console.log(err)
    })
}
putHeaderTeman =()=>{
    var putData= this.state.teman.map((item)=>{
        return(
            <div className="profile-friends container d-flex align-items-center p-3 my-3 text-white-50 bg-purple rounded shadow-sm">
                <img className="mr-3" src={`/image/avatar/${item.image}`} alt width={48} height={48} />
                <div className="lh-100">
                  <h6 className="mb-0 text-white lh-100">{item.username}</h6>
                  <small>Premium | level 1</small>
                  <small className="d-block text-left mt-3">
                  <button>Follow</button>
                </small>
                 
                </div>
                
                
              </div>
        )
    })
    return putData
}
putDataMatpel=()=>{
var putData = this.state.nilai.map((data)=>{
    return(
        <div className="media text-muted pt-3">
        {/* <img src={`./image/avatar/${item.image}`} className="bd-placeholder-img mr-2 rounded" width={32} height={32} /> */}
            <svg className="bd-placeholder-img mr-2 rounded" width={32} height={32} ><title>Placeholder</title><rect fill="#007bff" width="100%" height="100%" /></svg>
            <p className="media-body pb-3 mb-0 small lh-125 border-bottom border-gray">
            <strong className="d-block text-gray-dark">{data.Mapel}</strong>
                {/* {data.Score/5}%  ini bakal dipake kalo sudah ada konten materi dan ubah avg jadi sum di backend*/}
                {/* sementara pake yang dari try out dulu */}
                <ProgressBar percentage={data.Score}/> 
            </p>
        </div> 
    )
}
)
return putData
}
    render(){

        return(
            <div className='row '>
            <h2 className="border-bottom border-gray pb-2 mb-0">Profile @{this.props.nama}</h2>
                {this.putHeaderTeman()}
                <nav className="nav nav-underline">
                <a className="nav-link active" href="/homes?page=dashboard">Dashboard {this.props.nama}</a>
                <a className="nav-link" href="/homes?page=teman">
                  Following
                  <span className="badge badge-pill bg-light align-text-bottom">27</span>
                </a>
                <a className="nav-link" href="/homes?page=teman">
                  Followers
                  <span className="badge badge-pill bg-light align-text-bottom">27</span>
                </a>
              </nav>
                <div className="col-md-12 my-3 p-3 bg-white rounded shadow-sm">
                <h6 className="border-bottom border-gray pb-2 mb-0">  Materi yang dikuasai</h6>
                {this.putDataMatpel()} 
                <small className="d-block text-right mt-3">
                  <p>*Nilai berdasarkan akumulasi Tryout</p>
                </small>
              </div>
              <div className="col-md-12 my-3 p-3 bg-white rounded shadow-sm">
                <h6 className="border-bottom border-gray pb-2 mb-0">Aktivitas</h6>
   
                <small className="d-block text-right mt-3">
                  <a href="#">Semua Aktivitas</a>
                </small>
              </div>
            </div>
        )
    }
}

const mapStateToProps =(state)=>{
    return {
        username: state.auth.username,
        id : state.auth.id,
        image: state.auth.image
    };
}
export default connect(mapStateToProps,null)(ProfileUser);