import React, { Component } from 'react';
import './support/css/bootstrap.css';
import './support/css/bootstrap.min.css';
import './App.css';
import {Route} from 'react-router-dom';
import {connect} from 'react-redux';
import Cookies from "universal-cookie";
import {keepLogin,cookieChecked , tryout_masuk} from './actions';
import {withRouter} from 'react-router-dom';
import Header from './component/fitur/navbar';
import Homepage from './component/screen/homepage';
import login from './component/screen/login';
import Footer from './component/fitur/footer';
import HomeUser from './component/screen/homeuser';
import TryoutPage from './component/screen/tryout/tryoutPage'
import LoadingBar from 'react-redux-loading-bar'
import NavbarTryout from './component/screen/tryout/navbarTryOut'
import register from './component/screen/register';
import Verify from './component/screen/verify';
import Homechoose from './component/screen/homes';
import premium from './component/screen/premium';
import chat from './component/screen/chat';
const cookies = new Cookies();




class App extends Component {
  state = {
    navbar : true
  }
  componentDidMount(){
    const cookienya = cookies.get("dataUser");
    console.log(cookienya)
    if(cookienya !== undefined){
      this.props.keepLogin(cookienya);
    }
}

  render() {
    if(this.props.cookie && this.props.navbar && this.state.navbar){
      return (
        
        <div>  
            <Header/>
            <Route exact path='/' component={Homepage}/>
            <Route path='/masuk' component={login}/>
            <Route path='/daftar' component={register}/>
            <Route path='/siapptn' component={HomeUser}/>
            <Route path='/verify' component={Verify}/>
            <Route path='/home' component={Homechoose}/>
            <Route path='/premium' component={premium}/>
            <Route path='/chat' component={chat}/>
            <Footer/>
        
        </div>
      );
    }
    if(this.props.navbar == false){
      return(
        <div>
        <Route exact path='/tryout/:no' component={TryoutPage}/>
      </div>
      )
    }
    return(<LoadingBar/>);
  }
}
const mapStateToProps =(state)=>{
  return {
      cookie: state.auth.cookie,
      navbar : state.tryout.navbar
  };
}
export default withRouter(connect(mapStateToProps, {keepLogin,cookieChecked,tryout_masuk})(App));
