import React, {Component} from 'react';
import { Button, Form, Input} from 'reactstrap';
import {connect} from 'react-redux';
import Cookies from "universal-cookie";

const cookies = new Cookies();
class Premium extends Component{
    componentWillReceiveProps(newProps){
        console.log(newProps.username)
        if(newProps.username!==""){
            cookies.set('dataUser',newProps.username,{path: '/'})
            cookies.set('idUser',newProps.id,{path: '/'})
        }
      }
    onBtnLoginClick= ()=>{
        var username= this.refs.username.refs.tbusername.value;
        var password= this.refs.password.refs.tbpassword.value;
        this.props.onUserLogin({username, password});
    }
    renderError=()=>{
      if(this.props.loading == false){
        if(this.props.error.length>0){
          return <p className="alert alert-danger">{this.props.error}</p>
      }
      }
        
    }
    renderButton=()=>{
        if(this.props.loading){
            return <h3>Loading...</h3>
        }
        return <Button  onClick={this.onBtnLoginClick} type="submit" class="form-submit" >Log in</Button>

    }
    
    render(){
       
        return(
            <div className="container">
                <div className="pricing-header px-3 py-3 pt-md-5 pb-md-4 mx-auto text-center">
        <h1 className="display-4">Premium sekarang</h1>
        <p className="lead">Quickly build an effective pricing table for your potential customers with this Bootstrap example. It’s built with default Bootstrap components and utilities with little customization.</p>
      </div>
        <div className="card-deck mb-3 text-center">
          <div className="card mb-4 shadow-sm">
            <div className="card-header">
              <h4 className="my-0 font-weight-normal">Siapptn</h4>
            </div>
            <div className="card-body">
              <h1 className="card-title pricing-card-title">Rp 50.000 <small className="text-muted"></small></h1>
              <ul className="list-unstyled mt-3 mb-4">
                <li>Tryout utbk 2x</li>
                <li>Konsultasi jurusan via chat</li>
                <li>Pembahasan Tryout</li>
                <li>Rangking Nasional</li>
              </ul>
              <button type="button" className="btn btn-lg btn-block btn-primary">Get started</button>
            </div>
          </div>
          <div className="card mb-4 shadow-sm">
            <div className="card-header">
              <h4 className="my-0 font-weight-normal">Stan</h4>
            </div>
            <div className="card-body">
              <h1 className="card-title pricing-card-title">Rp 50.000<small className="text-muted"></small></h1>
              <ul className="list-unstyled mt-3 mb-4">
                <li>Tryout stan 2x</li>
                <li>Konsultasi jurusan via chat</li>
                <li>Pembahasan Tryout</li>
                <li>Rangking Nasional</li>
              </ul>
              <button type="button" className="btn btn-lg btn-block btn-primary">Get started</button>
            </div>
          </div>
        </div>
      </div>

        )
  }
}
const mapStateToProps =(state)=>{
    return {
        username: state.auth.username,
        id:state.auth.id,
        error: state.auth.error,
        loading: state.auth.loading
    
    };
}
export default connect(mapStateToProps,{})(Premium);