import React,{Component} from 'react'
import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, CardTitle, CardText, Row, Col } from 'reactstrap';
import Following from './page-teman/following';
import classnames from 'classnames';
import Followers from './page-teman/followers';
import Axios from 'axios';
import {connect} from 'react-redux';
class Teman extends Component{
  state={
    activeTab: '1'
  }
  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }

putdataFollowing=()=>{
 
  var putData = this.props.teman.map((data)=>{
      return(
          <p>Following {data.followingnya}</p>
      )
  })
  return putData
}
putdataFollowers=()=>{
  
  var putData = this.props.teman.map((data)=>{
      return(
          <p>Followers {data.followersnya}</p>
      )
  })
  return putData
}
    render(){
        return(
            <div>
        <Nav tabs>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '1' })}
              onClick={() => { this.toggle('1'); }}
            >
             {this.putdataFollowing()}
            </NavLink>
          </NavItem>
          <NavItem>
            <NavLink
              className={classnames({ active: this.state.activeTab === '2' })}
              onClick={() => { this.toggle('2'); }}
            >
              {this.putdataFollowers()}
            </NavLink>
          </NavItem>
        </Nav>
        <TabContent className="my-3 p-3 bg-white rounded shadow-sm" activeTab={this.state.activeTab}>
          <TabPane tabId="1">
            <Row>
              <Col sm="12">
                <Following/>
              </Col>
            </Row>
          </TabPane>
          <TabPane tabId="2">
            <Followers/>
          </TabPane>
        </TabContent>
      </div>
        )
    }
}
const mapStateToProps =(state)=>{
  return {
      username: state.auth.username,
      id:state.auth.id
  
  };
}
export default connect(mapStateToProps)(Teman);