import React, {Component} from 'react';
import '../../support/css/signin.css'
import { Button, Form, Input} from 'reactstrap';
import {Redirect} from 'react-router-dom'
import {connect} from 'react-redux';
import {onUserRegister} from '../../actions';
class Register extends Component{
      onRegisterClick = () => {
        this.props.onUserRegister({
            fullname:this.refs.fullname.refs.tbfullname.value, 
            phone:this.refs.phone.refs.tbphone.value,
            username: this.refs.username.refs.tbusername.value, 
            email: this.refs.email.refs.tbemail.value,
            password: this.refs.password.refs.tbpassword.value,
        });
        window.location.replace('/masuk')
    }
    renderError=()=>{
      if(this.props.loading == false){
        if(this.props.error.length>0){
          return <p className="alert alert-danger">{this.props.error}</p>
        }
      }
     
        
    }
    renderButton=()=>{
        if(this.props.loading){
            return <h3>Loading...</h3>
        }
        return <Button  onClick={this.onRegisterClick} type="button" className="form-submit btn btn-success" >Daftar</Button>

    }
    
    render(){
        return(
            <div className="signin">
            <div className="container">
                <div className="row justify-content-md-center">
                <div className="col-md-12">
                        <Form  className="form-signin text-center" id="login-form">
                        {/* <img className="mb-4" src="/docs/4.2/assets/brand/bootstrap-solid.svg" alt width={72} height={72} /> */}
                        <h1 className="h3 mb-3 font-weight-normal">Dilizents</h1>
                        <h2 className="h3 mb-3 font-weight-normal">Sign Up</h2>
                        <label htmlFor="inputFullname" className="label-signin">Nama Lengkap:</label>
                        <Input type="text" ref='fullname' innerRef='tbfullname' placeholder='Nama lengkapmu' className="form-control" id="fullname"/>
                        <label htmlFor="inputPhone" className="label-signin">No Ponsel</label>
                        <Input type="tel" ref='phone' innerRef='tbphone' placeholder='Nomor ponselmu' className="form-control" id="phone"/>
                        <label htmlFor="inputUsername" className="label-signin">Username:</label>
                        <Input type="text" ref='username' innerRef='tbusername' placeholder='username' className="form-control" id="usr"/>
                        <label htmlFor="inputEmail" className="label-signin">Email:</label>
                        <Input type="email" ref='email' innerRef='tbemail' placeholder='masukan emailmu disini' className="form-control" id="usr"/>
                        <label htmlFor="inputPassword" className="label-signin">Password:</label>
                        <Input type="password" ref='password' innerRef='tbpassword' placeholder='Password' className="form-control" id="pwd"/>
                      
                        {this.renderError()}
                        {this.renderButton()}
                       
                        {/* <p className="mt-5 mb-3 text-muted">© 2018-2019</p> */}
                        </Form>
                </div>
                    
                </div>
            </div>               
              </div>
        )
  }
}
const mapStateToProps =(state)=>{
    return {
        id:state.auth.id,
        error: state.auth.error,
        loading: state.auth.loading
    };
}
export default connect(mapStateToProps,{onUserRegister})(Register);