import React from 'react';
import {Link} from 'react-router-dom';
import {connect} from 'react-redux';
import {onUserLogout,keepLogin} from "../../actions";
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem  } from 'reactstrap';
  import Cookies from "universal-cookie";
  import { GoogleLogout } from 'react-google-login';
 

  const cookies = new Cookies();
class Header extends React.Component {
  state = { listUser:[], searchListUser: [] }
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
//   componentDidMount() {
//     axios.get('http://localhost:2001/user')
//         .then((res) => {
//             this.setState({ listUser: res.data, searchListUser: res.data })
//         }).catch((err) => {
//             console.log(err)
//         })
// }
  onLogOutSelect=()=>{
    this.props.onUserLogout();
    cookies.remove('dataUser');
  }

  render() {
    if(this.props.username === ''){
    return (
      <div>
        <Navbar light expand="md">
          <NavbarBrand  style={{color:'white'}} href="/">Dilizents</NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem>
                <NavLink style={{color:'white'}}  href="/daftar">Daftar</NavLink>
              </NavItem>
              <NavItem>
                <NavLink style={{color:'white'}}  href="/masuk">Masuk</NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
  return (
      <Navbar light expand="md">
        <Link to='/home'><NavbarBrand style={{color:'white'}}>Dilizents</NavbarBrand></Link>
        <NavbarToggler onClick={this.toggle} />
        <Collapse isOpen={this.state.isOpen} navbar>
        <Nav className="mr-auto" navbar>
        </Nav>
          <Nav className="ml-auto" navbar>
          
            <UncontrolledDropdown nav inNavbar>
              <DropdownToggle  style={{color:'white'}} nav caret>
                Hello, {this.props.username}
              </DropdownToggle>
              <DropdownMenu right>
                <Link to='/'><DropdownItem>
                  Option
                </DropdownItem></Link>
                <DropdownItem divider />
                <a href="/"><DropdownItem onClick={this.onLogOutSelect}>
                  Log Out
                </DropdownItem></a>
              </DropdownMenu>
            </UncontrolledDropdown>
          </Nav>
        </Collapse>
      </Navbar>
   );
  }
}
const mapStateToProps = (state) => {
  return{
    username:state.auth.username
  }
}

export default connect(mapStateToProps, {onUserLogout, keepLogin})(Header);