import React,{Component} from 'react'
import '../../../support/css/progress-bar.css'
const Filler =(props)=>{
  return <div className='filler'style={{width:`${props.percentage}%`}}>{props.percentage}%</div>
}
const ProgressBar =(props)=>{
  return(
    <div className="progress-bar">
      <Filler percentage={props.percentage}/>
    </div>
  )
}

class Dashboard extends Component{
    putDataMatpel=()=>{
        var putData = this.props.keyId.map((data)=>{
            return(
                <div className="media text-muted pt-3">
                {/* <img src={`./image/avatar/${item.image}`} className="bd-placeholder-img mr-2 rounded" width={32} height={32} /> */}
                  <svg className="bd-placeholder-img mr-2 rounded" width={32} height={32} ><title>Placeholder</title><rect fill="#007bff" width="100%" height="100%" /></svg>
                  <p className="media-body pb-3 mb-0 small lh-125 border-bottom border-gray">
                    <strong className="d-block text-gray-dark">{data.Mapel}</strong>
                      {/* {data.Score/5}%  ini bakal dipake kalo sudah ada konten materi dan ubah avg jadi sum di backend*/}
                      {/* sementara pake yang dari try out dulu */}
                      <ProgressBar percentage={data.Score}/> 
                  </p>
                </div> 
            )
        }
        )
        return putData
     }
    render(){

        return(
            <div className='row '>
                
                <div className="col-md-12 my-3 p-3 bg-white rounded shadow-sm">
                <h6 className="border-bottom border-gray pb-2 mb-0">  Materi yang dikuasai</h6>
                {this.putDataMatpel()} 
                <small className="d-block text-right mt-3">
                  <p>*Nilai berdasarkan akumulasi Tryout</p>
                </small>
              </div>
              <div className="col-md-12 my-3 p-3 bg-white rounded shadow-sm">
                <h6 className="border-bottom border-gray pb-2 mb-0">Aktivitas</h6>
   
                <small className="d-block text-right mt-3">
                  <a href="#">Semua Aktivitas</a>
                </small>
              </div>
            </div>
        )
    }
}

export default Dashboard;