import React,{Component} from 'react';
import {connect} from 'react-redux';
import axios from 'axios'
class Verify extends Component{
    state={
        form:''
    }
    onButtonClick=()=>{
        var job = this.refs.job.value;
        var instansi = this.refs.instansi.value;
        var provinsi = this.refs.provinsi.value;
        var id_user = this.props.id;
        var username = this.props.username;
        console.log(id_user,job,instansi,provinsi)
        if(id_user === '' || job === '' || instansi ==='' || provinsi == ''){
            this.setState({form:'Form wajib diisi'})
        }else{
            axios.post('http://localhost:2001/auth/userdata', {
            id_user,job,instansi,provinsi
            }).then((res)=>{
                    console.log(res.data);
                    axios.post('http://localhost:2001/auth/verified', {
                        username
                    }).then((res)=>{
                            console.log(res.data);
                            window.location.replace('/homes')
                    }).catch((err)=>{
                        console.log(err);
                    }) 
            }).catch((err)=>{
                console.log(err);
            }) 
        }
        
    }
    errorSubmit=()=>{
        if(this.state.form !== ''){
            return<p className="alert alert-danger">{this.state.form}</p>
        }
    }
    render(){
        if(this.props.status == 'unverified'){
            return(
                <div className=''>
                 <div className="jumbotron">
                   <div className="container">
                     <h3 className="display-1">Hai {this.props.username}</h3>
                     <p>Selamat datang di Dilizents lengkapi profilmu terlebih dahulu.</p>
                     {/* <p><button className="btn btn-primary btn-lg" href="#" role="button">Kirim ulang</button></p> */}
                   </div>
                 </div>
                 <div className="container">
                     <form>
                         <div className="form-group">
                             <label htmlFor="exampleInputEmail1">Siapakah kamu?</label>
                             <select ref='job' className="form-control" id="exampleFormControlSelect1">
                                 <option>Mahasiswa</option>
                                 <option>gapyear</option>
                                 <option>SMA</option>
                                 <option>SMP</option>
                             </select>
                             <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
                         </div>
                         <div className="form-group">
                             <label htmlFor="exampleInputPassword1">Nama Sekolah / instansi (Untuk gapyear dikosongkan)</label>
                             <input type="text" ref='instansi' className="form-control" id="exampleInputPassword1" placeholder="Masukan nama Sekolah / instansi" />
                         </div>
                         <div  className="form-group">
                             <label htmlFor="exampleInputPassword1">Nama Provinsi</label>
                             <select ref='provinsi' className="form-control" id="exampleFormControlSelect1">
                                 <option value="aceh">Aceh</option>
                                 <option value="Sumut">Sumatera Utara</option>
                                 <option value="sumbar">Sumatera Barat</option>
                                 <option value="Riau">Riau</option>
                                 <option value="Jambi">Jambi</option>
                                 <option value="Sumsel">Sumatera Selatan</option>
                                 <option value="Bengkulu">Bengkulu</option>
                                 <option value="Lampung">Lampung</option>
                                 <option value="BaBel">Kep. Bangka Belitung</option>
                                 <option value="kepRiau">Kepulauan Riau</option>
                                 <option value="Jakarta">Jakarta</option>
                                 <option value="Jabar">Jawa Barat</option>
                                 <option value="Banten">Banten</option>
                                 <option value="Jateng">Jawa Tengah</option>
                                 <option value="Yogyakarta">Yogyakarta</option>
                                 <option value="Jatim">Jawa Timur</option>
                                 <option value="Kalbar">Kalimantan Barat</option>
                                 <option value="Kalteng">Kalimantan Tengah</option>
                                 <option value="Kalsel">Kalimantan Selatan</option>
                                 <option value="Kaltim">Kalimantan Timur</option>
                                 <option value="Kaltra">Kalimantan Utara</option>
                                 <option value="Bali">Bali</option>
                                 <option value="NTT">Nusa Tenggara Timur</option>
                                 <option value="NTB">Nusa Tenggara Barat</option>
                                 <option value="Sulut">Sulawesi Utara</option>
                                 <option value="Sulteng">Sulawesi Tengah</option>
                                 <option value="Sulsel">Sulawesi Selatan</option>
                                 <option value="Sultengg">Sulawesi Tenggara</option>
                                 <option value="Sulbar">Sulawesi Barat</option>
                                 <option value="Gorontalo">Gorontalo</option>
                                 <option value="Maluku">Maluku</option>
                                 <option value="Maluku Utara">Maluku Utara</option>
                                 <option value="Papua">Papua</option>
                                 <option value="Papua Barat">Papua Barat</option>
                             </select>
                         </div>
                        {this.errorSubmit()}
                         <button type="button" className="btn btn-primary" onClick={this.onButtonClick}>Simpan datamu</button>
                     </form>
                 </div>
                </div>
               
             )
        }else{
            window.location.replace('/homes')
        }
    }
}
const mapStateToProps =(state)=>{
    return {
        username: state.auth.username,
        id : state.auth.id,
        image: state.auth.image,
        status:state.auth.status
    };
}
export default connect(mapStateToProps)(Verify);