import React,{Component} from 'react'
import {connect} from 'react-redux';
import '../../../support/css/diskusi.css'
import Axios from 'axios';
const now = new Date().toLocaleString();
class Diskusi extends Component{
state={
  diskusinow:[],
  diskusiList:[]
}
componentDidMount(){
    this.getDiskusiList()
}
getDiskusiList=()=>{
  Axios.get('http://localhost:2001/get-diskusi')
  .then((res)=>{
    this.setState({diskusiList:res.data})
    console.log(this.state.diskusiList)
  }).catch((err)=>{
    console.log(err)
  })
}
diskusiInput=()=>{
  var textarea = this.refs.pertanyaan.value;
  var id_user = this.refs.id_user.value;
  var date = now;
  Axios.post('http://localhost:2001/post-diskusi',{
     textarea, date, id_user
  }).then((res)=>{
    console.log(res.data)
    this.getDiskusiList()
    window.location.reload();
  }).catch((err)=>{
    console.log(err)
  })
}
 
putDiskusi=()=>{
  var putData = this.state.diskusiList.map((item)=>{
    return(
      <div className="media text-muted pt-3">
      <img src={`./image/avatar/${item.image}`} className="bd-placeholder-img mr-2 rounded" width={32} height={32} />
      <div className="media-body pb-3 mb-0 small lh-125 border-bottom border-gray">
        <div className="d-flex justify-content-between align-items-center w-100">
          <strong className="text-gray-dark">{item.fullname}</strong>
          <a href="#">Jawab</a>
        </div>
        <span className="d-block"><a href={`/homes?page=profile&&username=${item.username}`}>@{item.username}</a></span>
        <p>{item.textarea}</p>
      </div>
    </div>
    )
  })
  return putData
}
    render(){
        return(
            <div className="row justify-content-between">
              <div className="col-lg-8 my-3 p-3 bg-white rounded shadow-sm">
                <h6 className=" pb-2 mb-0">Diskusi</h6>
                <div className="text-area-bg media text-muted pt-3">
                <img src={`./image/avatar/${this.props.image}`} className="bd-placeholder-img mr-2 rounded" width={32} height={32} />
                  <div className="media-body pb-3 mb-0 small lh-125  border-gray">
                    <div className="d-flex justify-content-between align-items-center w-100">
                     <textarea ref='pertanyaan' innerRef='tpertanyaan' placeholder="tanyakan disini" className="input-diskusi"/>
                     <input ref='username' type='hidden' value={this.props.username}/>
                     <input ref='fullname' type='hidden' value={this.props.fullname}/>
                     <input ref='image' type='hidden' value={this.props.image}/>
                     <input ref='id_user' type='hidden' value={this.props.id}/>
                    </div>
                    <div className="justify-content-between">
                    <button type='btn ' onClick={this.diskusiInput} className='btn btn-primary mt-2'>Post</button>
                    </div>
                  </div>
                </div>
                {this.putDiskusi()}
                <small className="d-block text-center mt-3">
                  <a href="#">Mulai diskusikan disini</a>
                </small>
              </div>
              <div className="col-lg-3 my-3 p-3 bg-white rounded shadow-sm sugestion">
                <h6 className="border-bottom border-gray pb-2 mb-0">Sugestions</h6>
                <div className="media text-muted pt-3">
                  <svg className="bd-placeholder-img mr-2 rounded" width={32} height={32} xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img" aria-label="Placeholder: 32x32"><title>Placeholder</title><rect fill="#007bff" width="100%" height="100%" /><text fill="#007bff" dy=".3em" x="50%" y="50%">32x32</text></svg>
                  <div className="media-body pb-3 mb-0 small lh-125 border-bottom border-gray">
                    <div className="d-flex justify-content-between align-items-center w-100">
                      <strong className="text-gray-dark">Full Name</strong>
                      <a href="#">Follow</a>
                    </div>
                    <span className="d-block">@username</span>
                    <p>test</p>
                  </div>
                </div>
                <div className="media text-muted pt-3">
                  <svg className="bd-placeholder-img mr-2 rounded" width={32} height={32} xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img" aria-label="Placeholder: 32x32"><title>Placeholder</title><rect fill="#007bff" width="100%" height="100%" /><text fill="#007bff" dy=".3em" x="50%" y="50%">32x32</text></svg>
                  <div className="media-body pb-3 mb-0 small lh-125 border-bottom border-gray">
                    <div className="d-flex justify-content-between align-items-center w-100">
                      <strong className="text-gray-dark">Full Name</strong>
                      <a href="#">Follow</a>
                    </div>
                    <span className="d-block">@username</span>
                    <p>test</p>
                  </div>
                </div>
                <div className="media text-muted pt-3">
                  <svg className="bd-placeholder-img mr-2 rounded" width={32} height={32} xmlns="http://www.w3.org/2000/svg" preserveAspectRatio="xMidYMid slice" focusable="false" role="img" aria-label="Placeholder: 32x32"><title>Placeholder</title><rect fill="#007bff" width="100%" height="100%" /><text fill="#007bff" dy=".3em" x="50%" y="50%">32x32</text></svg>
                  <div className="media-body pb-3 mb-0 small lh-125 border-bottom border-gray">
                    <div className="d-flex justify-content-between align-items-center w-100">
                      <strong className="text-gray-dark">Full Name</strong>
                      <a href="#">Follow</a>
                    </div>
                    <span className="d-block">@username</span>
                    <p>test</p>
                  </div>
                </div>
                <small className="d-block text-right mt-3">
                  <a href="#">Semua Aktivitas</a>
                </small>
              </div>
            </div>
        )
    }
}
const mapStateToProps =(state)=>{
  return {
      username: state.auth.username,
      image: state.auth.image,
      fullname:state.auth.fullname,
      id:state.auth.id
  };
}
export default connect(mapStateToProps)(Diskusi);
