import axios from 'axios';
import {
    USER_LOGIN_SUCCESS, 
    USER_NOT_FOUND, 
    LOGIN_SYSTEM_ERROR,
    LOGIN_LOADING, LOGOUT,
    REGISTER_LOADING,
    COOKIE_CHECKED,
    SELECT_POPOK,
    HARUS_DIISI,
    USERNAME_TIDAK_TERSEDIA,
    REGISTER_SUCCESS
} from './types';
export const onUserLogout=()=>{
    return{type: LOGOUT }
}
export const keepLogin =(username)=>{
    return(dispatch)=> {
        axios.get('http://localhost:2001/auth/keeplogin',{
            params:{
                username
            }
        }).then((res)=>{
            console.log(res.data[0].status)
            if(res.data.length > 0 ){
                dispatch({
                    type: USER_LOGIN_SUCCESS, 
                    payload: {  
                        username: res.data[0].username,
                        id:res.data[0].id,
                        fullname : res.data[0].nama,
                        email: res.data[0].email,
                        phone: res.data[0].phone,
                        image: res.data[0].image,
                        status:res.data[0].status
                     }
                })
            }
        })
    }
}
export const cookieChecked =()=>{
    return{type: COOKIE_CHECKED }
}
export const onUserLogin = ({username, password}) =>{
    return(dispatch)=>{
        if(username == '' || password == ''){
            return dispatch({type : HARUS_DIISI})
        }
        dispatch({type: LOGIN_LOADING})
        axios.post('http://localhost:2001/auth/signin', {
            username,
            password
        }).then((res)=> {
            console.log(res)
            if(res.data.length>0){
                dispatch({ type: USER_LOGIN_SUCCESS, 
                    payload: { 
                        username: res.data[0].username,
                        id:res.data[0].id,
                        fullname : res.data[0].nama, 
                        email: res.data[0].email,
                        phone: res.data[0].phone,
                        image: res.data[0].image,
                        status:res.data[0].status
                    } });
            }else{
                dispatch({type: USER_NOT_FOUND})
            }
        }).catch((err)=>{
            console.log(err)
            dispatch({type: LOGIN_SYSTEM_ERROR})
        }) 
    }
}
export const onUserRegister = ({fullname,username,email,phone, password})=>{
    return (dispatch)=>{
        dispatch({type: REGISTER_LOADING})
        if(fullname === '' || username ==='' || password == '' || phone == ''||email==''){
            dispatch({type: HARUS_DIISI})
        }else{
            axios.post('http://localhost:2001/auth/register', {
           fullname, username, email, password, phone
            }).then((res)=>{
                console.log(res)
                if(res.data.status === 'error'){
                    dispatch({type: USERNAME_TIDAK_TERSEDIA})
                }
                else{
                    console.log(res.data);
                    dispatch({type: USER_LOGIN_SUCCESS, payload: res.data})
                }     
            }).catch((err)=>{
                console.log(err);
                dispatch({type: LOGIN_SYSTEM_ERROR})
            }) 
        }
        
    }
}
