import React,{Component} from 'react';
import {connect} from 'react-redux';
import '../../support/css/homeuser.css';
import queryString from 'query-string';
import Axios from 'axios'
import Cookies from "universal-cookie";
import {Redirect} from 'react-router-dom'
import ModalPaket from '../fitur/modalpaket'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter,Input } from 'reactstrap'
const cookies = new Cookies();
class Homechoose extends Component{
state = {
    dataPaket : [],
    datauser:[],
    job:'',
    modal: false
}

toggle() {
    this.setState({
      modal: !this.state.modal
    });
  }
  
  onClickBtn = () => {
    this.props.tryout_masuk()
    cookies.set('navbar',true,{path: '/'})
  }
componentDidMount(){
const cookienya = cookies.get("dataUser")
const idnya = cookies.get("idUser")
this.getUserData(idnya)
this.toggle = this.toggle.bind(this);
}
getUserData=(id)=>{
    
    Axios.get('http://localhost:2001/auth/getdata/'+id)
    .then((res) => {
        console.log(res.data)
        this.setState({datauser : res.data})
        Axios.post('http://localhost:2001/paket/getpaket'
        ).then((res) => {
            console.log(res.data)
            this.setState({
                dataPaket : res.data,
                job:res.data[0].job
            })
        }).catch((err) => {
            console.log(err)
        })   
    }).catch((err) => {
        console.log(err)
    })
}
putPaket=()=>{
    const paket = this.state.dataPaket.map(item=>{
        if(item.status == ''){
            return(
                <div className="col-md-3">
                <a  onClick={this.toggle} className='text-white no-underline  '>
                    <div className={`card mb-4 shadow-sm ${item.nama}`}>
                    <div className="card-body card-height">
                    <h3 className="bold pb-4">{item.nama.toUpperCase()}</h3>
                   <center> <img src={item.gambar} width='200' className='align-items-center'/><div className="centered"><h2 className='card-title bg-comingsoon'>{item.status}</h2></div></center>
                   
                        <p className="card-text">{item.deskripsi}</p>
                       
                    </div>
                    </div>
                    </a>
                    <Modal isOpen={this.state.modal} toggle={this.toggle} className={this.props.className}>
          <ModalBody>
          <div className='container'>
                <h2>Premium {item.nama} sekarang untuk belajar lebih maksimal?</h2>
            </div>
          </ModalBody>
          <ModalFooter>
            <a href={`/premium`}>
            <Button color="primary" onClick={this.onClickBtn}>Yakali engga premium</Button>
            </a> {' '}
            <a href={`/${item.nama.toLowerCase()}`}>
            <Button color="secondary" onClick={this.toggle}>gratis dulu deh</Button></a>
          </ModalFooter>
        </Modal>
                </div>
                )
        }
        return(
            <div className="col-md-3">
            <a className='text-white no-underline  ' >
                <div className={`card mb-4 shadow-sm ${item.nama}`}>
                <div className="card-body card-height">
                <h3 className="bold pb-4">{item.nama.toUpperCase()}</h3>
               <center> <img src={item.gambar} width='200' className='align-items-center'/><div className="centered"><h2 className='card-title bg-comingsoon'>{item.status}</h2></div></center>
               
                    <p className="card-text">{item.deskripsi}</p>
                   
                </div>
                </div>
                </a>
            </div>
            )
      
    })
    return paket
}
    render(){
        const cookienya = cookies.get("dataUser")
        if(cookienya == ''){
            return <Redirect to='/masuk' />
        }else if(this.props.status == 'unverified'){
            console.log(this.props.status)
            return <Redirect to='/verify' />
        }else{
            return(
                <div className="bg-light">
                <div className="nav-scroller bg-white shadow-sm">
                  <nav className="nav nav-underline">
                    <a className="nav-link active" href="/home">Home</a>
                    <a className="nav-link" href="/premium">Harga Paket</a>
                  </nav>
                </div>
                <div className="container d-flex align-items-center p-3 my-3 text-white-50 background-dilizents rounded shadow-sm">
                    <img className="mr-3" src={`/image/avatar/${this.props.image}`} alt width={48} height={48} />
                    <div className="lh-100">
                      <h6 className="mb-0 text-white lh-100">{this.props.username}</h6>
                      <small>Premium | level 1</small>
                    </div>
                  </div>
                <main role="main">
                <div className="album  bg-light">
                <div className="container">
                <h3 className='mb-3'>Direkomendasikan untuk siswa sekolah menengah atas dan perguruan tinggi</h3>
                    <div className="row">
                   {this.putPaket()}
                  
                   
                    </div>
                </div>
                </div>
                </main>
              </div>
            )
        }
    }
}
const mapStateToProps =(state)=>{
    return {
        username: state.auth.username,
        id : state.auth.id,
        image: state.auth.image,
        status:state.auth.status
    };
}
export default connect(mapStateToProps,null)(Homechoose);